s.calibration.calibrated\" class=\"hint-text\">{{this.status.calibration.captures}}&nbsp;Captures</span>\n        <span v-else class=\"hint-text\">Calibrated</span>\n      </div>\n</template>\n\n<script>\n  import Session from \"../src/Vision/Session\";\n  import {defineComponent} from \"vue\";\n\n  export default defineComponent({\n    data () {\n      return {\n\n      }\n    },\n    props: {\n      session: Session,\n      status: Object\n    },\n    mounted () {\n\n    },\n    methods: {\n      captureCalibrationPoints() {\n        this.session.calibration.setCaptureNextcalibrationPoints()\n      },\n      calibrate() {\n        this.session.calibrate()\n      },\n      reset() {\n        this.session.resetCalibration()\n      },\n    }\n  });\n</script>\n\n<style scoped>\n  .controls {\n    display: flex;\n    gap: 2px;\n    align-items: center;\n  }\n\n  button {\n    background-color: rgba(255,255,255,0.8);\n    border-radius: 2px;\n    border: none;\n    padding: 5px 10px;\n    margin-bottom: 5px;\n  }\n\n  button:disabled {\n    background-color: rgba(255,255,255,0.8);\n  }\n\n  .capture-image {\n    background-color: rgba(255,0,0,0.8);\n    color: white;\n    box-shadow: 0 0 4px rgba(255,64,64,1);\n  }\n\n  .capture-image:disabled {\n    background-color: rgba(128, 0, 0, 0.6);\n    border: none; /* some kind of blue border */\n    box-shadow: none;\n    color: #ccc;\n  }\n\n  .hint-text {\n    font-size: 0.8rem;\n    color: white;\n    margin-left: 10px;\n    text-shadow: 0 1px 1px rgba(0,0,0,0.5);\n  }\n</style>\n"],"sourceRoot":""}]);
