___CSS_LOADER_EXPORT___.push([module.id, "\n.setup[data-v-41e93bf0] {\n    position: relative;\n    box-sizing: border-box;\n    max-width: 100%;\n    max-height: 80vh;\n    overflow-y: auto;\n    width: 300px;\n    padding: 10px 20px 20px 20px;\n    background-color: rgba(255,255,255,0.8);\n    backdrop-filter: blur(5px);\n    border-radius: 4px;\n    font-size: 0.8rem;\n}\n.close-button[data-v-41e93bf0] {\n    position: absolute;\n    top: 6px;\n    right: 10px;\n    font-size: 1.5rem;\n    line-height: 1.5rem;\n    cursor: pointer;\n}\n.camera-selection[data-v-41e93bf0] {\n    width: 100%;\n}\n.status-value[data-v-41e93bf0] {\n    float: right;\n}\n.title[data-v-41e93bf0] {\n    font-weight: lighter;\n    text-align: center;\n}\nbutton[data-v-41e93bf0] {\n    background-color: rgba(32,32,32,0.8);\n    color: white;\n    border-radius: 30px;\n    padding: 10px 15px;\n    margin-bottom: 5px;\n    cursor: pointer;\n    border: none;\n}\nbutton[data-v-41e93bf0]:focus {\n    border: none;\n    outline: none;\n}\nhr[data-v-41e93bf0] {\n    border: none;\n    border-top: 1px solid rgba(0,0,0,0.4);\n}\n", "",{"version":3,"sources":["webpack://./components/ARSetup.vue"],"names":[],"mappings":";AA4GE;IACE,kBAAkB;IAClB,sBAAsB;IACtB,eAAe;IACf,gBAAgB;IAChB,gBAAgB;IAChB,YAAY;IACZ,4BAA4B;IAC5B,uCAAuC;IACvC,0BAA0B;IAC1B,kBAAkB;IAClB,iBAAiB;AACnB;AAEA;IACE,kBAAkB;IAClB,QAAQ;IACR,WAAW;IACX,iBAAiB;IACjB,mBAAmB;IACnB,eAAe;AACjB;AAEA;IACE,WAAW;AACb;AAEA;IACE,YAAY;AACd;AAEA;IACE,oBAAoB;IACpB,kBAAkB;AACpB;AAEA;IACE,oCAAoC;IACpC,YAAY;IACZ,mBAAmB;IACnB,kBAAkB;IAClB,kBAAkB;IAClB,eAAe;IACf,YAAY;AACd;AAEA;IACE,YAAY;IACZ,aAAa;AACf;AAEA;IACE,YAAY;IACZ,qCAAqC;AACvC","sourcesContent":["<template>\n  <div class=\"setup\">\n    <div v-if=\"status\">\n      <div class=\"close-button\" @click=\"close\"></div>\n      <div v-if=\"showHowToCalibrate\">\n        <h2 class=\"title\">Camera Calibration</h2>\n        <p>To make VueXR know how to project virtual elements to the real world, we have to calibrate your camera first.</p>\n        <ol>\n          <li>Print or display this <a href=\"https://docs.opencv.org/master/pattern.png\" target=\"_blank\">chessboard pattern</a> on another device.</li>\n          <li>Take a steady aim at the pattern with your camera.</li>\n          <li>Press the <strong>Capture</strong> button below.</li>\n          <li>Repeat for at least 4 other different perspectives.</li>\n          <li>Press the <b>Calibrate</b> button.</li>\n        </ol>\n        <div style=\"margin-top: 20px; text-align: center;\">\n          <button class=\"reset-button\" @click=\"showHowToCalibrate = false\">Back</button>\n        </div>\n      </div>\n      <div v-else>\n        <h2 class=\"title\">AR Status</h2>\n        <div class=\"status-line\">\n          Video\n          <span v-if=\"this.status.feed.error\" class=\"status-value\" :title=\"this.status.feed.error\"></span>\n          <span v-else class=\"status-value\"></span>\n        </div>\n        <select class=\"camera-selection\" style=\"margin-top: 5px\" v-model=\"selected\" @change=\"this.selectCamera\">\n          <option disabled value=\"\">Please select a Camera</option>\n          <option v-for=\"item in availableCameras\" :value=\"item.deviceId\">\n            <span v-if=\"item.label\">{{item.label}}</span>\n            <span v-else>{{item.deviceId}}</span>\n          </option>\n        </select>\n        <hr>\n        <div class=\"status-line\">\n          Accelerometer\n          <span v-if=\"this.status.motion && this.status.motion.acceleration.error\" class=\"status-value\" :title=\"this.status.motion.acceleration.error\"></span>\n          <span v-else class=\"status-value\"></span>\n        </div>\n        <hr>\n        <div class=\"status-line\">\n          Gyroscope\n          <span v-if=\"this.status.motion && this.status.motion.gyro.error\" class=\"status-value\" :title=\"this.status.motion.gyro.error\"></span>\n          <span v-else class=\"status-value\"></span>\n        </div>\n        <hr>\n        <div class=\"status-line\">\n          Computer Vision\n          <span v-if=\"!this.status.worker.initialized\" class=\"status-value\"></span>\n          <span v-else class=\"status-value\"></span>\n        </div>\n        <hr>\n        <div class=\"status-line\">\n          Calibration\n          <span v-if=\"!this.status.calibration.calibrated\" class=\"status-value\"></span>\n          <span v-else class=\"status-value\"></span>\n        </div>\n        <div v-if=\"this.status.calibration.calibrated\" style=\"margin-top: 20px; text-align: center;\">\n          <button class=\"reset-button\" @click=\"this.reset\">Reset Calibration</button>\n        </div>\n        <div v-else-if=\"!this.status.feed.error && this.status.worker.initialized\" style=\"margin-top: 20px; text-align: center;\">\n          <button class=\"reset-button\" @click=\"showHowToCalibrate = true\">How to calibrate</button>\n        </div>\n      </div>\n    </div>\n  </div>\n</template>\n\n<script>\n  import Session from \"../src/Vision/Session\";\n  import {defineComponent} from \"vue\";\n\n  export default defineComponent({\n    data () {\n      return {\n        selected: null,\n        showHowToCalibrate: false,\n      }\n    },\n    computed: {\n      availableCameras () {\n        return this.status.feed.available\n      },\n    },\n    props: {\n      session: Session,\n      status: Object\n    },\n    methods: {\n      selectCamera (event) {\n        this.session.feed.selectCamera(this.selected)\n      },\n      reset() {\n        this.session.resetCalibration()\n      },\n      close() {\n        this.$emit('close')\n      }\n    },\n    mounted () {\n      this.selected = this.session.feed.feedStatus.selected;\n    },\n    updated() {\n      this.selected = this.session.feed.feedStatus.selected;\n    }\n  });\n</script>\n\n<style scoped>\n  .setup {\n    position: relative;\n    box-sizing: border-box;\n    max-width: 100%;\n    max-height: 80vh;\n    overflow-y: auto;\n    width: 300px;\n    padding: 10px 20px 20px 20px;\n    background-color: rgba(255,255,255,0.8);\n    backdrop-filter: blur(5px);\n    border-radius: 4px;\n    font-size: 0.8rem;\n  }\n\n  .close-button {\n    position: absolute;\n    top: 6px;\n    right: 10px;\n    font-size: 1.5rem;\n    line-height: 1.5rem;\n    cursor: pointer;\n  }\n\n  .camera-selection {\n    width: 100%;\n  }\n\n  .status-value {\n    float: right;\n  }\n\n  .title {\n    font-weight: lighter;\n    text-align: center;\n  }\n\n  button {\n    background-color: rgba(32,32,32,0.8);\n    color: white;\n    border-radius: 30px;\n    padding: 10px 15px;\n    margin-bottom: 5px;\n    cursor: pointer;\n    border: none;\n  }\n\n  button:focus {\n    border: none;\n    outline: none;\n  }\n\n  hr {\n    border: none;\n    border-top: 1px solid rgba(0,0,0,0.4);\n  }\n</style>\n"],"sourceRoot":""}]);
